<template>
  <div class="youtube">
    <div class="row">
      <div class="col-lg-6 col-md-12" :key="i" v-for="(product, i) in list">
        <div class="mb-4 card">
          <div class="ratio ratio-16x9">
            <iframe :src="'https://www.youtube.com/embed/' + product.id " allowfullscreen id="ytplayer" type="text/html" frameborder="0"></iframe>
          </div>
          <div class="card-body">
            <div class="card-title h5">{{ product.snippet.title }}</div>
            <p class="card-text"></p></div>
          <div class="list-group list-group-flush">
            <div class="text-muted list-group-item" v-html="'Ï°∞ÌöåÏàò : ' + product.statistics.viewCount" ></div>
            <div class="text-muted list-group-item" v-html="'üëç ' + product.statistics.likeCount + ' üëé ' + product.statistics.dislikeCount + ' ‚úç ' + product.statistics.commentCount "  ></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from '@/../config.yml';
export default {
  name: "Youtube", //Ïª¥Ìè¨ÎÑåÌä∏ Ïù¥Î¶Ñ
  inject : ['url'],
  components: {}, //Îã§Î•∏ Ïª¥Ìè¨ÎÑåÌä∏ ÏÇ¨Ïö© Ïãú import(Î∞∞Ïó¥Î°ú Îì±Î°ù)
  data() {
    return {
      list: [], //Î∂àÎü¨Ïò® apiÎ•º Îã¥ÏùÑ list
      regions: ''
    };
  },
  created() { //Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÏÉùÏÑ±ÎêòÎ©¥ Ïã§Ìñâ
    this.getList(this.url);
  },
  mounted() {
    this.getRegions();
  },
  methods: {
    async getList(url) { //apiÌò∏Ï∂ú
      this.list = await this.$api(url, "get");
      this.list = this.list.items;
    },
    async getRegions() {
      const params = {
        'key': config.googleKey
      }
      this.regions = await this.$api('https://www.googleapis.com/youtube/v3/i18nRegions', "get", params);
      this.regions = this.regions.items
    }
  }
}
</script>

